import { Component, OnChanges, OnInit, HostListener, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormControl } from '@angular/forms';
import { MatSidenav } from '@angular/material';
import { Router } from '@angular/router';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  // host:{
  //   '(document:click)': 'onClick($event)',
  // }
})
export class AppComponent {
  toggle: boolean = null;
  title = 'joyTube';
  screenWidth: any


  constructor(private _eref: ElementRef, public router:Router) {
    this.onresize()
  }

  @ViewChild('sidenav') sidenav: MatSidenav
  close() {
    this.sidenav.close();
  }

  // searchFormControl = new FormControl('', [
  //   Validators.email,
  // ]);
  isMobile(): number {
    if (this.screenWidth > 800) {
      return 0
    }
    if(this.screenWidth<450){
      return 2
    }
    return 1
  }

  
  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    const clickedInside = this._eref.nativeElement.contains(targetElement);
    if (!clickedInside) {
          (document.querySelector('#ddc') as HTMLElement).style.display = 'none';
      this.toggle = true;
    }
   }



  @HostListener('window:resize', ['$event'])
  onresize(event?) {
    this.screenWidth = window.innerWidth
  }



  dropdown() {
    if (this.toggle == true) {


      (document.querySelector('#ddc') as HTMLElement).style.display = 'block';
      this.toggle = false;
    } else if(this.toggle==false) {
      (document.querySelector('#ddc') as HTMLElement).style.display = 'none';
      this.toggle = true;

    } else{
      (document.querySelector('#ddc') as HTMLElement).style.display = 'block';
      this.toggle = false;
    }
  }

}
